body {
  margin: 0;
  font-family: 'Montserrat', sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

:root{
  --primary-color: #67B637;
  --secondary-color: #4F70C2;
  --success-color: #28a745;
  --info-color: #17a2b8;
  --warning-color: #ffc107;
  --danger-color: #dc3545;
  --light-color: #eeeeee;
  --dark-color: #222222;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

header nav{
  display: flex;
  justify-content: space-between;
  align-items: center;
  padding: .5rem 1rem;
  background-color: var(--dark-color);
  color: var(--primary-color);
  font: 1rem 300 'Montserrat', sans-serif ;
}
header nav img{
  height: 50px;
}
.design{
  background-color: var(--dark-color);
  padding: 2rem;
  display: flex;
  flex-direction: column;
  transition: background-color 0.2s ease-in-out;
  width: 60%;
  margin: 1rem auto;
  box-shadow: rgba(149, 157, 165, 0.2) 0px 1px 4px;
}
/* .design:nth-child(even){
  background-color: var(--dark-color);

} */
/* .design:hover{
  background-color: #2222220e;
  color: var(--light-color);
} */
.design .info{
  display: flex;
  justify-content: space-between;
  align-items: center;
  flex-wrap: wrap;
}
.design .info h2{
  font-size: 1.5rem;
  margin: 0 0 0.5rem 0;
  font-weight: 700;
  width: 100%;
  color: var(--primary-color);
}
.design .info p{
  font-size: 1.1rem;
  font-weight: 400;
  color: var(--secondary-color);
  padding: 0 1rem;
  margin: 0;
  text-align: center;
}
.design .demo{
  display: block;
  margin-top: 1rem;
  /* background-color: #222222; */
  width: 100%;
  min-height: 100px;
  height: 100%;
  overflow-x: scroll;
}

.design .demo *{
  max-width: calc(100vw - 2rem);
  
}

.design .code{
  display: flex;
  margin-top: 1rem;
  width: 100%;
  justify-content: space-between;
  flex-wrap: wrap;
  align-items: center;
}
.design .code button{
  background-color: var(--primary-color);
  color: white;
  padding: 0.5rem 1rem;
  border: none;
  font-size: 1rem;
  font-weight: 500;
  cursor: pointer;
  margin-right: 1rem;
  transition: background-color 0.3s;
  text-align: center;
}
.design .code span{
  flex-grow: 1000;
  display: flex;
}

.design .show{
  margin: 1rem 0;
  flex-grow: 1;
}

.design .code button:hover{
  background-color: var(--secondary-color);
}


.filter-sort{
  display: flex;
  justify-content: space-between;
  align-items: center;
  padding: 1rem 2rem;
  background-color: var(--dark-color);
  color: var(--primary-color);
  font: 1rem 300 'Montserrat', sans-serif ;
  width: 60%;
  margin: 1rem auto;
}
.filter-sort select{
  padding: 0.5rem 1rem;
  font-size: 1rem;
  font-weight: 500;
  border: none;
  background-color: var(--light-color);
  color: var(--dark-color);
  cursor: pointer;
  
  margin: 0 5px;
}
@media (max-width: 768px){
  .design{
    width: 75%;
  }
  .design .code button{
    display: flex;
    justify-content: space-between; 

  }
  .design .code button:last-child{
    margin-right: 0;
  }

  .design .show{
    margin-right: 0 !important;
    text-align: center !important;
  }
  .design .code span{
    justify-content: space-between;
  }
  .filter-sort{
    padding: 1rem !important;
    width: calc(75% + 2rem) !important;
  }
  .filter-sort select{
    width: 100%;
  }
  .design .info span p{
    text-align: left;
  }
  .design .info span{
    width: 100%;
    margin: 0 0 0.5rem 0;
  }
  nav img{
    height: 40px !important;
  }
  nav{
    text-align: center;
  }
}
